<template>
	<div id="home">
		<router-view></router-view>
		<van-tabbar v-model="active" fixed @change="activeChange">
			<!--<van-tabbar-item icon="wap-home" to="/" :replace=true>首页</van-tabbar-item>-->
			<!--<van-tabbar-item icon="records" :replace=true>同城</van-tabbar-item>-->
			<!--<van-tabbar-item icon="contact" :replace=true>消息</van-tabbar-item>-->
      <!--<van-tabbar-item icon="records" :replace=true>订单</van-tabbar-item>-->
      <!--<van-tabbar-item icon="contact" :replace=true>个人中心</van-tabbar-item>-->
      <div class="van-tabbar-item" @click="toIndex" v-show="show">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_sy.png"/>
        </div>
        <div class="van-tabbar-item__text">首页</div>
      </div>
      <div class="van-tabbar-item" @click="reIndex" v-show="!show">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_sy_pre.png"/>
        </div>
        <div class="van-tabbar-item__text color_40a3f3">首页</div>
      </div>

      <div class="van-tabbar-item" @click="toCityWide" v-show="show1">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_tc.png"/>
        </div>
        <div class="van-tabbar-item__text">同城</div>
      </div>
      <div class="van-tabbar-item" @click="reCityWide" v-show="!show1">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_tc_pre.png"/>
        </div>
        <div class="van-tabbar-item__text color_40a3f3">同城</div>
      </div>

      <div class="van-tabbar-item" @click="toMessage" v-show="show2">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_xx.png"/>
        </div>
        <div class="van-tabbar-item__text">消息</div>
      </div>
      <div class="van-tabbar-item" @click="reMessage" v-show="!show2">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_xx_pre.png"/>
        </div>
        <div class="van-tabbar-item__text color_40a3f3">消息</div>
      </div>

      <div class="van-tabbar-item" @click="toOrder" v-show="show3">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_dd.png"/>
        </div>
        <div class="van-tabbar-item__text">订单</div>
      </div>
      <div class="van-tabbar-item" @click="reOrder" v-show="!show3">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_dd_pre.png"/>
        </div>
        <div class="van-tabbar-item__text color_40a3f3">订单</div>
      </div>

      <div class="van-tabbar-item" @click="toUser" v-show="show4">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_grzx.png"/>
        </div>
        <div class="van-tabbar-item__text">个人中心</div>
      </div>
      <div class="van-tabbar-item" @click="reUser" v-show="!show4">
        <div class="van-tabbar-item__icon">
          <img src="static/img/icon_grzx_pre.png"/>
        </div>
        <div class="van-tabbar-item__text color_40a3f3">个人中心</div>
      </div>

		</van-tabbar>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
        show:true,
        show1:true,
        show2:true,
        show3:true,
        show4:true,
				active: parseInt(localStorage.getItem("activeBar")),
				loading: false
			}
		},
		methods: {
			activeChange(active) {
				localStorage.setItem("activeBar", active);
			},
      toIndex(){
        this.show=!this.show;
        this.show1=true;
        this.show2=true;
        this.show3=true;
        this.show4=true;
        this.$router.push('/');
      },
      reIndex(){
        this.$router.push('/');
      },
      toCityWide(){
        this.show1=!this.show1;
        this.show=true;
        this.show2=true;
        this.show3=true;
        this.show4=true;
        this.$router.push('cityWide');
      },
      reCityWide(){
        this.$router.push('cityWide');
      },
      toMessage(){
        this.show2=!this.show2;
        this.show4=true;
        this.show3=true;
        this.show1=true;
        this.show=true;
        this.$router.push('message');
      },
      reMessage(){
        this.$router.push('message');
      },
      toOrder(){
        this.show3=!this.show3;
        this.show4=true;
        this.show2=true;
        this.show1=true;
        this.show=true;
        this.$router.push('order');
      },
      reOrder(){
        this.$router.push('order');
      },
      toUser(){
        this.show4=!this.show4;
        this.show3=true;
        this.show2=true;
        this.show1=true;
        this.show=true;
        this.$router.push('user');
      },
      reUser(){
        this.$router.push('user');
      },
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



